@import url(https://fonts.googleapis.com/css?family=Poppins:200,300,400,500,600,700,800,900&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}h2{font-family:'Poppins', sans-serif}a{text-decoration:none}.back-video{position:absolute;left:0;top:0;width:100%;height:100%;object-fit:cover;opacity:0.8}.text-red{color:brown}.text-red:hover{background-color:aqua}.loading-ring{display:inline-block;width:80px;height:80px}.loading-ring:after{content:" ";display:block;width:64px;height:64px;margin:8px;border-radius:50%;border:6px solid rgba(224,96,96,0.883);border-color:rgba(224,96,96,0.883) transparent rgba(224,96,96,0.883) transparent;-webkit-animation:loading-ring 1.2s linear infinite;animation:loading-ring 1.2s linear infinite}@-webkit-keyframes loading-ring{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}@keyframes loading-ring{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}

.about_img{width:200px}

.projects{display:flex;flex-direction:column;align-items:center;margin:0 auto;width:100vw;max-width:1400px}.projects ul{display:flex;flex-direction:column;align-items:flex-start}.projects ul li{display:flex;list-style:none;width:100%;margin:1rem}.projects ul li .project{display:flex;width:100%}.projects ul li img{width:48%;margin-right:1.5rem;margin-left:1.5rem}.projects ul li:nth-child(even) .project{flex-direction:row-reverse}.project-about{display:flex;flex-direction:column;justify-content:space-between;align-items:center;width:47%;background-color:#d4e6d2}.project-about .project_about-title{width:90%;margin-top:1rem;text-transform:uppercase;text-align:center;margin-bottom:0;line-height:1.5;font-family:"Halis Rounded","Helvetica Neue",Helvetica,Arial,sans-serif;color:#2e2e2e;font-size:1.5rem;font-weight:700}.project-about .project_about-subtitle{text-align:center;font-weight:400;font-size:1rem;letter-spacing:.15em;text-transform:uppercase;font-family:"Proxima Nova","Helvetica Neue",Helvetica,Arial,sans-serif;color:dimgray;margin-bottom:1rem;line-height:1.5}.project-about .project-link{color:#fff;background:#fd4b46;border:1px solid #fd4b46;width:90px;height:22px;margin-bottom:0.5rem;display:flex;align-items:center;font-size:13px;font-family:"Halis Rounded", "Helvetica Neue", Helvetica, Arial, sans-serif;font-weight:700;transition:all .2s ease-in-out;text-transform:uppercase;border-radius:25px;padding:5px 16px 5px}.project-about .project-link:hover{background:#fc1a14;border-color:#f40903;color:#fff}

